<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" class="input-with-select">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderlist" border>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{scope.row.create_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column lable="">
          <template>
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
            <el-button type="success" icon="el-icon-location" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 20, 30, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <el-dialog title="" :visible.sync="dialogVisible" width="30%" @close='addressDialogClosed'>
        <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
          <el-form-item label="/" prop="address1">
          <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
          </el-form-item>
          <el-form-item label="" prop="address2">
            <el-input v-model="addressForm.address2"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import cityData from './citydata.js'

export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules: {
        address1: [{ require: true, trigger: 'blur' }],
        address2: [{ require: true, trigger: 'blur' }]
      },
      cityData,
      total: 0,
      orderlist: [],
      dialogVisible: false
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })

      if (res.meta.status !== 200) {
        return this.$message.error('failed')
      }

      this.total = res.data.total
      this.orderlist = res.data.goods
      console.log(this.total)
    },
    handleSizeChange(i) {
      this.queryInfo.pagesize = i
      this.getOrderList()
    },
    handleCurrentChange(i) {
      this.queryInfo.pagenum = i
      this.getOrderList()
    },
    showBox() {
      this.dialogVisible = true
    },
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    }
  }
}

</script>
<style scoped>
.el-cascader{
  width: 100%;
}
</style>
